  getMarkerByAddressListAMap(addressList: string[], city?) {
    const option: any = {};
    if (city) option.city = city;
    const geocoder = new AMap.Geocoder({});

    return new Observable<{ point: number[][] }[]>(observer => {

      forkJoin(addressList.map(address => {
        return new Observable<any>(ob => {
          geocoder.getLocation(address, (status, res) => {
            let point;
            if (status !== 'complete') {
              point = [];
            } else {
              const first = res.geocodes[0];
              point = [first.location.lng, first.location.lat];
            }
            ob.next({ point });
            ob.complete();
          });
        })
      }))
        .subscribe(res => {
          observer.next(res);
          observer.complete();
        })
    });
  }

  getMarkerByLocationList(locationList: NetworkLocation[]) {
    const option: any = {};
    return new Observable<{ point: number[] }[]>(observer => {

      forkJoin<GeoResult>(locationList.map(location => {
        return this.googleGeo(location.streetAddress)
          .pipe(
            catchError(o => null)
          );
      }))
        .subscribe((res) => {
          const results = res.map((result, i) => {
            let point: number[] = []
            if (result) {
              point = [result.geometry.location.lng, result.geometry.location.lat];
            }

            const location = locationList[i];
            return {
              point,
              icon: 'icon-dingwei',
              data: location,
              template: {
                title: location.tenantName,
                body: `${[location.streetAddress, location.city, location.province, location.country].filter(o => o).join(', ')}`
              },
            }
          });
          observer.next(results);
          observer.complete();
        })
    });
  }

